//  src/components/Root 
import { createGlobalStyle, css } from "styled-components";

const GlobalStyle = createGlobalStyle`
  ${({ theme: { colors: c } }) => css`
    :root,
    input {
      color: ${c.text.main};
    }
    body {
      margin: 0;
    }
    html {
      scroll-behavior: smooth;
    }
    body,
    input {
      background: ${c.bg.main};
    }
  `}
`;

export default GlobalStyle;
