import React, { ComponentPropsWithoutRef } from "react";
import { Link } from "gatsby";
import styled, { css } from "styled-components";
import classNames from "classnames";
import { page } from "../../constants/conf";
import { WindowLocation } from "@reach/router";

const Box = styled.header`
  ${({ theme: { colors: c } }) => css`
    background: linear-gradient(to bottom right, ${c.secondary.main}, ${c.primary.main});
    color: white;
  `}
`;

const Nav = styled.nav`
  display: flex;
  align-items: flex-end;
  max-width: 1200px;
  padding: 10px 20px;
  margin: auto;
  a {
    color: white;
    margin: 0 10px;
    text-decoration: none;
    position: relative;

    &:hover,
    &:active {
      color: ${(p) => p.theme.colors.primary.refer};
    }

    &.active::after {
      content: "";
      position: absolute;
      bottom: -10px;
      left: -5px;
      right: -5px;
      height: 5px;
      border-radius: 2.5px;
      background-color: ${(p) => p.theme.colors.yellow.refer};
    }
  }
`;

const Title = styled(Link)`
  font-size: 1.8rem;
  margin-right: 20px;
`;

const navs = [
  {
    path: page.record,
    name: "",
  },
  {
    path: page.search,
    name: "",
  },
  {
    path: page.draft,
    name: "",
  },
];

interface HeaderProps extends ComponentPropsWithoutRef<"header"> {
  location: WindowLocation;
}

export default function Header({ location }: HeaderProps) {
  return (
    <Box>
      <Nav>
        <Title to="/"></Title>
        {navs.map((v) => (
          <Link key={v.path} to={v.path} className={classNames({ active: v.path === location.pathname })}>
            {v.name}
          </Link>
        ))}
      </Nav>
    </Box>
  );
}
